<template>
  <el-scrollbar class="scrollbar">
    <div v-for="(log, index) in logMessages" :key="index">
      {{ log }}
    </div>
  </el-scrollbar>
</template>

<script>
export default {
  data() {
    return {
      logMessages: [], // 存储日志信息
      ws: null,
    };
  },
  mounted() {
    this.initWebSocket();
  },
  beforeUnmount() {
    this.closeWebSocket(); // 组件销毁时关闭 WebSocket
  },
  methods: {
    initWebSocket() {
      if (this.ws) {
        console.warn('WebSocket already initialized');
        return;
      }
      this.logMessages = [];
      const baseURL = this.$axios.defaults.baseURL.replace(/^http/, 'ws').replaceAll('/api', '');
      const wsURL = `${baseURL}/logs`;
      this.ws = new WebSocket(wsURL);

      this.ws.onopen = () => {
        console.log('WebSocket connected');
        this.startHeartbeat();
      };

      this.ws.onmessage = (event) => {
        const message = event.data;
        if (!this.logMessages.includes(message)) {
          this.logMessages.push(message);
        }
        if (this.logMessages.length > 30) {
          this.logMessages.shift();
        }
      };

      this.ws.onerror = (error) => {
        console.error('WebSocket error:', error);
        this.reconnectWebSocket();
      };

      this.ws.onclose = () => {
        console.warn('WebSocket closed. Attempting to reconnect...');
        this.stopHeartbeat();
        this.reconnectWebSocket();
      };

      this.startConnectionChecker();
    },
    closeWebSocket() {
      if (this.ws) {
        this.ws.close();
        this.ws = null;
      }
      this.stopHeartbeat();
      this.stopConnectionChecker();
      this.logMessages = [];
    },
    startHeartbeat() {
      if (!this.heartbeatInterval) {
        this.heartbeatInterval = setInterval(() => {
          if (this.ws.readyState === WebSocket.OPEN) {
            this.ws.send('ping');
          }
        }, 30000);
      }
    },
    stopHeartbeat() {
      if (this.heartbeatInterval) {
        clearInterval(this.heartbeatInterval);
        this.heartbeatInterval = null;
      }
    },
    reconnectWebSocket() {
      this.closeWebSocket();
      setTimeout(() => {
        console.log('Reconnecting WebSocket...');
        this.initWebSocket();
      }, 5000);
    },
    startConnectionChecker() {
      if (!this.connectionCheckInterval) {
        this.connectionCheckInterval = setInterval(() => {
          if (!this.ws || this.ws.readyState === WebSocket.CLOSED) {
            console.warn('WebSocket disconnected. Attempting to reconnect...');
            this.reconnectWebSocket();
          }
        }, 10000);
      }
    },
    stopConnectionChecker() {
      if (this.connectionCheckInterval) {
        clearInterval(this.connectionCheckInterval);
        this.connectionCheckInterval = null;
      }
    },
  },

};
</script>

<style scoped>
.scrollbar {
  height: 700px;
  border: 1px solid #ccc;
  padding: 10px;
}
</style>
